/*///////////// STYLING FOR IPSUM APP /////////////*/
.IpsumApp
{
  width: 100%;
  min-height: 100vh;
  position: absolute;
  background-color: #DACA4C;
}
.header-container {
  padding-top: 6em;
  padding-left: 20px;
  text-align: center;
}

.IpsumApp-container {
  margin: 40px auto;
  width: 70%;
  border: 4px solid #C4462E;
  border-radius: 8px;
  padding: 15px;
  background-color: #ffe;
}

.IpsumApp .main-header {
  font-size: 68px;
  text-transform: uppercase;
  font-family: 'Dosis', sans-serif;
  font-weight: bold;
  color: #4085AA;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.IpsumApp .sidebar-container {
  margin-top: 25px;
  font-weight: bold;
  text-align: center;  
  font-size: 20px;
  font-family: 'Roboto Condensed', sans-serif;
}
.IpsumApp select {
  border: none;
  background: none; 
  font-size: 20px;
}
#lorem-container {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 24px;
}

.IpsumApp .sub-heading {

 margin-left: 15px;
}
