@font-face {
  font-family: Bagnard Regular;
  src: url('../fonts/Bagnard.otf');
}


nav.work {
  background-color: #338733;
}

nav.work ul li, nav.work ul li a {
  color: #ffffb7;
  
}
.menuBtn
{
 
  font-weight: bold;

}

.Work-wrapper {
	text-align: center;
	width: 100%;
	position: absolute;
  background-color: #338733;
  min-height: 100vh;
}
.Work-header {
position: relative;
margin: 80px auto;
width: 80%;
text-align: center;
padding-bottom: 180px;
padding-top: 20px;
line-height: 50px;
}

.Work-header h1, .Work-header h2, .Work-link {
   color: #ffa5cc;
}
.Work-header h1 {
  font-size: 60px;
 font-family: 'Bagnard Regular', serif;
}
.Work-header h2 {
  font-size: 40px;
  font-family: 'Gotu', serif;  
  transition-duration: 0.2s;
}
.Work-link {
  text-decoration: none;
}
.Work-link:hover {
  font-size: 48px;
  transition-duration: 0.2s;
  cursor: pointer;
}

.Big-and p {
	font-size: 80px;
  margin-top: 12px;
  margin-bottom: 12px;
}

