@font-face {
  font-family: Bagnard Regular;
  src: url('../fonts/Bagnard.otf');
}


nav {
  background-color: #33335d;
}

nav ul li, nav ul li a {
  color: #ffffb7;
  font-family: 'Gotu', serif;
  
}
.menuBtn
{
 
  font-family: 'Bagnard Regular', serif;
  font-weight: bold;
  color: #ffffb7;

}

.Landing-wrapper {
	text-align: center;
	width: 100%;
	position: absolute;
  background-color: #33335d;
  min-height: 100vh;
}
.Landing-header {
position: relative;
margin: 80px auto;
width: 80%;
text-align: center;
padding-bottom: 180px;
padding-top: 20px;
line-height: 50px;
}
.Landing-header h1 {
  font-size: 60px;
}
.Landing-header h2 {
  font-size: 46px;
}
.Landing-header h1, .Landing-header h2, .Big-and p {
  font-family: 'Bagnard Regular', serif;
  color: #ffffb7;
	margin-left: 5px;
}
.Big-and p {
	font-size: 80px;
  margin-top: 12px;
  margin-bottom: 12px;
}

