@font-face {
  font-family: Bagnard Regular;
  src: url('../fonts/Bagnard.otf');
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}
.body {
  height: 100%;
  position: absolute;
  width: 100%;
  
}


.hidden {
  display: none !important;
}

.closed {
  display: none;
}

.open li  {
  /*display: inline;*/
}
.open a {
  color: #000;
  text-decoration: none;
  font-size: 18px;
}


ul, li a
{
  list-style-type: none;
  text-decoration: none;
}


nav ul {
  margin-bottom: 0px;
  padding-left: 5px;
  display: inline;
}
nav ul li {
  
  display: inline;
  margin-right: 20px;
}
.menuBtn
{
  background: none;
  border: none;
  font-size: 30px;  
  font-weight: bold;
}

